.popular {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px; /* Increased gap for better spacing */
    height: auto; /* Changed from fixed height to auto */
    width: 100%;
    padding-bottom: 20px; /* Added padding for spacing */
    margin-bottom: 80px;
    margin-left: 50px;
}

.popular h1 {
    color: #171711;
    margin-top: 50px;
    font-size: 50px;
    font-weight: 600;    
}

.popular hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.popular-item {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap; /* Allow items to wrap */
    justify-content: center; /* Center items */
    gap: 40px;
    width: 100%;
    max-width: 1200px; /* Limit width for better centering */
}
